ulsar o obstaculizar los procesos; un elemento clave en la dirección estratégica es direccionar y movilizar todos los esfuerzos posibles para hacer que las cosas ocurran, generando acción. Cuando somos capaces de movilizarnos, entonces será posible afrontar cualquier objetivo y encauzar todos los recursos disponibles hacia él.")]),_c('p',[_vm._v("Las siguientes preguntas orientadoras, sirven para empezar a enfocar los ejes estratégicos de acción, de acuerdo con la meta propuesta: ")]),_c('ul',{staticClass:"fa-ul"},[_c('li',[_c('span',{staticClass:"fa-li"},[_c('i',{staticClass:"txt-orange fas fa-check"})]),_vm._v(" ¿En qué aspectos se encuentra satisfecho?")]),_c('li',[_c('span',{staticClass:"fa-li"},[_c('i',{staticClass:"txt-orange fas fa-check"})]),_vm._v(" ¿Cuál aspecto está siendo su mayor debilidad?")]),_c('li',[_c('span',{staticClass:"fa-li"},[_c('i',{staticClass:"txt-orange fas fa-check"})]),_vm._v(" ¿Qué necesita para mejorar?")]),_c('li',[_c('span',{staticClass:"fa-li"},[_c('i',{staticClass:"txt-orange fas fa-check"})]),_vm._v(" ¿Qué oportunidades tiene? ")])])]),_c('div',{staticClass:"col-md-6 order-1 order-lg-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema2/slider_2_1_06.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0 order-2 order-lg-1"},[_c('h4',[_vm._v("Seguimiento y evaluación de resultados")]),_c('p',[_vm._v("Se debe hacer revisión periódica para verificar que lo planeado, está siendo ejecutado en los tiempos establecidos y si hay variaciones se debe hacer un plan de mejora que permita corregir errores y direccionar nuevamente la planeación para el logro del objetivo. Por último, se debe hacer una evaluación de los resultados de los planes; es clave analizar cómo fue la ejecución de las acciones esto ayuda a consolidar los procesos y aprender de los errores, los que puede llevar a reajustar el plan inicial; por supuesto, todo en func